import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { IMapKeyValue, IOptionsPsActionHyperlink, IOptionsPsContainerLookupOptionComponentExposed, IOptionsPsLabel } from 'src/app/commonSRC/psServices/models/ps-common-interface';
import { PsTemplateListPage } from 'src/app/commonSRC/psTemplates/ps-template-list/ps-template-list.template';
import { CommonBussinessConstant } from '../../psServices/models/ps-common-bussiness-constant';
import { PsCommonBusinessSettings } from '../../psServices/models/ps-commonBusiness.settings';
import { IOptionsPsPayIdListExposed } from './ps-my-payId.component.interface';

@Component({
  selector: 'ps-my-payId',
  templateUrl: './ps-my-payId.component.html',
  styleUrls: ['./ps-my-payId.component.scss'],
})
export class PsMyPayIdComponent extends PsTemplateListPage implements OnInit {
  @Input() options: IOptionsPsPayIdListExposed;
  @Output() public onClickCard = new EventEmitter<any>();
  public labelValuesMap = new Map<string, IMapKeyValue>();
  public headerMap = new Map<string, IMapKeyValue>();
  @Output() reloadFct = new EventEmitter<string>();
  public requestMap = new Map<string, string>();
  public editRequestMap = new Map<string, string>();
  showSingleItem = false;

  public cardLabelOptions: IOptionsPsLabel = {
    labelKey: 'no_pay_id_available'
  };
  public addPayIdOptions: IOptionsPsActionHyperlink = {
    route: 'add-payId',
    labelKey: 'add_pay_id_key',
    iconOptions: {
      iconName: 'pay-id',
      labelOptions: {
        labelKey: 'add_Pay_id_key'
      }
    },
    pageOptions: {
      operId: CommonBussinessConstant.ADD_payId,
      title: 'add_pay_id_key',
      iconName: 'pay_id',
    }
  };


  ngOnInit() {
    super.init();
    this.populateHeaderValueMap();
    if (this.options.showOnlyList === null || this.options.showOnlyList === undefined) {
      this.options.showOnlyList = false;
    }

    if (this.options && this.options.listOfOptions) {
      this.options.listOfOptions.map((element, index) => element['rowIndex'] = index);
      this.options.itemList = this.options.listOfOptions;
    }

  }

  getContainerLookUpOptions(val) {
    return this.prepareLookUp(val);
  }

  private prepareLookUp(card: any): any {
    const lookupDetails: IOptionsPsContainerLookupOptionComponentExposed = {};
    lookupDetails.currencyFlag = 'currencyDesc';
    lookupDetails.labelsValueMap = this.labelValuesMap;
    lookupDetails.headerMap = this.headerMap;
    lookupDetails.formGroup = this.options.group;
    lookupDetails.itemList = this.options.itemList;
    lookupDetails.isEditable = this.options.isEditable;
    lookupDetails.translateSubTitle = this.options.translate;
    lookupDetails.matCardCssSelectors = card.matCardCssSelectors;
    lookupDetails.editActionUrl = PsCommonBusinessSettings.serviceUrl.personalizationNicknameEndPoint;
    lookupDetails.requestMap = this.requestMap;
    lookupDetails.editRequestMap = this.editRequestMap;
    lookupDetails.actionDetailsOptions = [
      {
        id: 'edit',
        label: 'edit_key',
        actionHyperlink: {
          group: this.options.group,
          iconOptions: {
            iconName: 'edit1',
            labelOptions: {
              labelKey: 'edit_key'
            }
          },
          route: 'add-payId',
          navigationOptions: {
            queryParams: { readOnlypage: false },
          },
          pageOptions: {
            operId: CommonBussinessConstant.ADD_payId,
            iconName: 'edit1',
            title: 'edit_key'
          }
        },
        cssClass: 'edit-payId'
      },
      ((card.statusCode != CommonBussinessConstant.ACCOUNTS_STATE_ACTIVE) ?
        {
          id: 'link',
          buttonIcon: 'arrow-circle-right',
          actionUrl: PsCommonBusinessSettings.serviceUrl.linkAccount,
          cssClass: 'fab-edit-button',
          labelOptions: {
            labelKey: 'link_key'
          },
          label: 'link_key',
          iconName: 'arrow-circle-right',
          actionType: 'link',
          alertType: 'prompt',
          alertStyle: 'warning',
          reloadList: true
        } : {}),
      ((card.statusCode == CommonBussinessConstant.ACCOUNTS_STATE_ACTIVE) ?
        {
          id: 'delink',
          label: 'delink_key',
          actionHyperlink: {
            group: this.options.group,
            iconOptions: {
              iconName: 'arrow-circle-right',
              labelOptions: {
                labelKey: 'delink_key'
              }
            },
            route: 'delink-payId',
            pageOptions: {
              operId: CommonBussinessConstant.LINK_OR_DELINK_OPER_ID,
              iconName: 'arrow-circle-right',
              title: 'delink_pay_id_key',
            },
            navigationOptions: {
              queryParams: { action: "delink" },
            },
          }
        } : {}),
      {
        id: 'transfer_money',
        label: 'transfer_money_key',
        actionHyperlink: {
          group: this.options.group,
          iconOptions: {
            iconName: 'arrow-circle-right',
            labelOptions: {
              labelKey: 'transfer_money_key'
            }
          },
          route: 'payId-transfer',
          pageOptions: {
            operId: CommonBussinessConstant.PAYID_TRANSFER_OPER_ID,
            iconName: 'arrow-circle-right',
            title: 'transfer_money_key',
          },
          preCallFunction: {
            func() {
              return new Promise<any>((resolve, reject) => {
                resolve(this.executionClass.prepareTransaction(lookupDetails.actionDetailsOptions[3].actionHyperlink));
              });
            },
            executionClass: this,
            params: []
          },
        }
      },

    ];
    lookupDetails.reloadFunction = {
      func() {
        return new Promise<any>((resolve, reject) => {
          resolve(this.executionClass.reloadList());
        });
      },
      executionClass: this,
      params: []
    }

    return lookupDetails;
  }
  reloadList() {
    this.reloadFct.emit();
  }

  private populateHeaderValueMap() {
    let nickNameMap: IMapKeyValue = {};
    let availableBalanceMap: IMapKeyValue = {};
    let linkedAccountMap: IMapKeyValue = {};
    let headerTitleMap: IMapKeyValue = {};
    let headerSubtitleMap: IMapKeyValue = {};
    let payIdTypeMap: IMapKeyValue = {};
    let mobileNumberMap: IMapKeyValue = {};
    let emailMap: IMapKeyValue = {};
    let phoneNumberMap: IMapKeyValue = {};

    nickNameMap = {
      key: "nickName",
      value: '----',
      formGroupMap: {
        placeHolder: 'enter_card_type',
        fcName: 'nickName',
      }
    };

    availableBalanceMap = {
      key: 'availableBalance',
      value: ''
    };

    linkedAccountMap = {
      key: 'linkedAccount',
      value: ''
    };

    payIdTypeMap = {
      key: 'payIdTypeDescriptionConcat',
      value: ''
    };

    mobileNumberMap = {
      key: 'mobileNumber',
      value: ''
    };

    emailMap = {
      key: 'email',
      value: ''
    };
    phoneNumberMap = {
      key: 'phoneNumber',
      value: ''
    };

    headerTitleMap = {
      key: 'title',
      value: 'nickName',
      isEdit: true,

    };

    // key here will be used as label in look up details component
    this.labelValuesMap.set('nickName_key', nickNameMap);
    this.labelValuesMap.set('available_balance_key', availableBalanceMap);
    this.labelValuesMap.set('linked_account_key', linkedAccountMap);
    this.labelValuesMap.set('payid_type_key', payIdTypeMap);
    this.labelValuesMap.set('mobile_number_key', mobileNumberMap);
    this.labelValuesMap.set('email_key', emailMap);
    this.labelValuesMap.set('phone_key', phoneNumberMap);
    // will be used as header to template card in look up details component
    // this.headerMap.set('title', headerTitleMap,);
    this.editRequestMap.set('newNickName', 'nickName');
    this.editRequestMap.set('type', 'A');;

  }
  public updateCard(item: any) {
    if (item && this.options.listOfOptions) {
      this.options.itemList = this.options.listOfOptions.filter(payId => payId === item);
      this.options.item = this.options.itemList[0];
      this.showSingleItem = !this.showSingleItem;
      this.options.showItemPopUp = !this.options.showItemPopUp;
      // this.options.showSelectedCard = false;
    }
    this.onClickCard.emit(this.options);
  }

  prepareTransaction(actionCard) {
    actionCard.navigationOptions.queryParams.ScreenVO = {
      ...actionCard.navigationOptions.queryParams.ScreenVO,
      fromPayId: actionCard.navigationOptions.queryParams.nickName,
      fromPayId_lookupKey: actionCard.navigationOptions.queryParams.nickName,
    };
  }




}


